body{
  margin: 0;
  padding: 0;
}
.happy_img{
  width: 90%;
  margin: 0;
}
.happy_box{
  width: 100%;    
  box-sizing: border-box;
  border: 1px solid rgb(208, 40, 162);
  border: 2px solid ridge;
  text-align: center;
  overflow: hidden;
}
.happy_title{
  font-size: large;
  padding: 10px;
  text-align: justify;
}

.tab_nav_a {
  box-shadow: 0px 10px 10px seashell;
  font-size: 20px;
  color: #444;
  position: relative;
  padding: 0 13px;
  font-weight: bold;
  position: fixed;
  width: 100%;
  height: 60px;
  background: url('//s3a.bytecdn.cn/neihan/resource/neihan_web/static/image/site-v2/logo_741b09e.png') no-repeat center #ff819f;
  background-size: 70px 50px;
  box-sizing: border-box;
}
a{
  text-decoration: none;
}
.nav_r{
  margin-top: 15px;
  float: right;
  color: seashell;
}
.nav_l{
  float: left;
  margin-top: 15px;
  color: seashell;
}
.content{
  margin-top: 60px;
}
.happy_text{
  text-align: center;
  margin-bottom: 0;
  padding: 0;
}
